import React,{useEffect,useState,useContext} from 'react'
import { ProductContext } from '../../pages/oniContext'
import { Form, FormGroup, Label, Input } from 'reactstrap';

export const InputBoxes = () => {
    const pCR = useContext(ProductContext);
    return (
      <div>
        {pCR.activeAddOns.map((service, index) => (
          <FormGroup id={service.id} check inline>
            <Label id={service.id} check>
              <Input type="checkbox" /> {service.title}
            </Label>
          </FormGroup>
        ))}
      </div>
    );
}
